.object-card{
  &__img{
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    padding-bottom: 125.8%;
    margin-bottom: 22px;
    &:hover{
      &:before,
      .object-card__link{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(20, 20, 20, 0.6), rgba(20, 20, 20, 0.6));
      z-index: 2;
      transition: all .3s;
      opacity: 0;
    }
    img{
      position: absolute;
      left: 0;
      top: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
  &__link{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 3;
    transition: all .3s;
    opacity: 0;
  }
  &__price{
    font-size: 24px;
    line-height: 150%;
    margin-top: 12px;
  }
}

.stage-card{
  min-height: 223px;
  background: #222222;
  border-radius: 10px;
  position: relative;
  &:nth-child(1),
  &:nth-child(3){
    .stage-card__arrow{
      left: 100%;
      top: 50%;
      transform: translate(-2px,-50%);
    }
  }
  &:nth-child(2){
    .stage-card__arrow{
      left: 50%;
      top: 100%;
      transform: translate(-50%,-2px);
    }
  }
  &:nth-child(3){
    grid-column: 2/3;
    grid-row: 2/3;
  }
  &:nth-child(4){
    grid-column: 3/5;
    grid-row: 2/3;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px));
    gap: 30px;
    .stage-card__arrow{
      left: 75%;
      bottom: 100%;
      transform: translate(-50%,2px);
    }
  }
  &:nth-child(5){
    grid-column: 4/5;
    grid-row: 1/2;
    background-color: $primary;
    .stage-card__num{
      color: rgba(#fff,.2);
    }
  }
  &__content{
    padding: 22px 35px;
  }
  &__arrow{
    position: absolute;
    z-index: 2;
  }
  &__num{
    line-height: 150%;
    color: rgba(#fff,.1);
    position: relative;
    margin-bottom: 26px;
  }
  &__icon{
    position: absolute;
    left: 0;
    bottom: 1px;
  }
  &__steps{
    font-size: 20px;
    line-height: 160%;
    color: $primary;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px 35px 10px 0;
    &>*{
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 992px){
  .object-card{
    &__img{
      margin-bottom: 20px;
    }
    &__link{
      padding: 0 32.5px;
    }
    &__price{
      margin-top: 20px;
      font-size: 18px;
    }
  }
}
