.footer{
  background: #222222;
  border-radius: 10px 10px 0px 0px;
  padding: 112px 0;
  &__grid{
    display: grid;
    grid-template-columns: 2.1fr 1.6fr .8fr 1.4fr;
    gap: 10px;
  }
  &__link{
    display: block;
    font-size: 14px;
    line-height: 35px;
  }
  &__btn{
    width: 100%;
    height: 53px;
  }
  &__bottom{
    margin-top: 67px;
  }
  &__socials{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-item{
      &:hover{
        svg{
          transform: translateY(-10px);
        }
      }
    }
  }
}
