.display1,
.display2,
.display3{
  line-height: 120%;
  font-family: $font2;
}

.display1{
  font-size: 52px;
}

.display2{
  font-size: 42px;
}

.display3{
  font-size: 40px;
  line-height: 130%;
}

.title-left{
  span{
    padding-left: 80px;
    display: block;
  }
}

a.accent-color:hover{
  color: darken($primary,20%);
}

.accent-color{
  color: $primary;
}

.bold{
  font-weight: 700;
}

.semi-bold{
  font-weight: 600;
}

.medium{
  font-weight: 500;
}

.small-text{
  font-size: 14px;
  line-height: 140%;
}
.medium-text{
  font-size: 16px;
  line-height: 150%;
}
.large-text{
  font-size: 18px;
  line-height: 150%;
}

.small-text,
.medium-text,
.large-text{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 22px;
    }
  }

}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 28px;
  }

  .display2{
    font-size: 20px;
  }

  .title-left{
    span{
      padding-left: 40px;
    }
  }

  .medium-text,
  .large-text{
    font-size: 12px;
  }

  .small-text{
    font-size: 10px;
  }
}
