.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  margin-top: -113px;
  overflow: hidden;
  &__inner{
    padding: 160px 0 100px;
    position: relative;
  }
  &__title{
    max-width: 572px;
    color: $primary;
    span{
      color: #fff;
    }
  }
  &__info{
    margin-top: 60px;
    display: flex;
    align-items: center;
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 40px;
      }
    }
    &-num{
      font-size: 40px;
      line-height: 120%;
      font-family: $font2;
      margin-right: 24px;
      opacity: 0.4;
    }
  }
  &__bottom{
    margin-top: 72px;
    display: flex;
    align-items: center;
  }
  &__desc{
    margin-left: 25px;
    max-width: 325px;
  }
  &__img{
    position: absolute;
    right: 40px;
    bottom: 0;
    width: 372px;
    &:before{
      content: '';
      position: absolute;
      bottom: -18px;
      left: 50%;
      transform: translateX(-50%);
      width: 357px;
      height: 357px;
      filter: blur(142px);
      background-color: $primary;
      border-radius: 0;
      z-index: 2;
    }
    &-man{
      display: block;
      width: 100%;
      position: relative;
      z-index: 3;
    }
    &-rounds{
      position: absolute;
      bottom: -150px;
      left: 50%;
      transform: translateX(-50%);
      width: 621px;
    }
  }
  &__owner{
    position: absolute;
    background: #FFFFFF;
    border-radius: 6px;
    padding: 12px 27px;
    bottom: 272px;
    right: 100%;
    transform: translateX(40px);
  }
  &__name{
    white-space: nowrap;
    color: $black;
  }
}

.difficulties {
  padding: 70px 0;
  margin: 70px 0 0 0;
  overflow: hidden;
  position: relative;
  &__bg {
    position: absolute;
    top: 30%;
    left: -10%;
    background: $primary;
    opacity: 0.8;
    filter: blur(112px);
    width: 210px;
    height: 210px;
  }
  &__inner {
    background: $primary;
    border-radius: 10px;
    padding: 70px 100px;
    position: relative;
  }
  &__img {
    position: absolute;
    bottom: 50px;
    right: -128px;
    height: 464px;
    img{
      width: auto;
      height: 100%;
    }
  }
  &__title {
    max-width: 876px;
    color: #222222;
    span {
      color: #FFFFFF;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 15px;
    margin-top: 60px;
  }
  &__item {
    padding: 30px;
    background: #222222;
    border-radius: 10px;
    &-text {
      margin-top: 25px;
    }
  }
}

.who{
  padding: 70px 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(41.5% - 15px) calc(58.5% - 15px);
    gap: 30px;
  }
  &__title{
    max-width: 380px;
    span{
      display: block;
    }
  }
  &__desc{
    margin-top: 60px;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__info{
    display: grid;
    grid-template-columns: 1.24fr 1fr;
  }
  &__img{
    padding-bottom: 100%;
    position: relative;
    flex-shrink: 0;
    &:before{
      content: '';
      position: absolute;
      width: 50%;
      height: 100%;
      left: 50%;
      top: 0;
      background: #222222;
    }
    img{
      border-radius: 50%;
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      z-index: 2;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px 34px;
    background: #222222;
    border-radius: 0px 10px 10px 0px;
  }
  &__desc2{
    margin-top: 50px;
  }
}

.way{
  padding: 70px 0;
  &__inner{
    display: grid;
    grid-template-columns: 46.5% 49%;
    gap: 4.5%;
  }
  &__right{
    display: flex;
    flex-direction: column;
  }
  &__desc{
    margin-top: 50px;
  }
  &__bottom{
    padding-top: 25px;
    margin-top: auto;
    display: flex;
    align-items: center;
  }
  &__btn{
    padding: 0;
    width: 240px;
    margin-right: 64px;
    flex-shrink: 0;
  }
  &__instagram{
    display: flex;
    align-items: center;
    svg{
      margin-right: 10px;
    }
    span{
      text-decoration-line: underline;
    }
  }
  &__items{
    margin-top: 70px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  &__item{
    min-height: 140px;
    border-right: 1px solid rgba(#fff,.1);
    padding: 0 30px;
    display: flex;
    align-items: flex-start;
    &:first-child{
      padding-left: 0;
    }
    &:last-child{
      padding-right: 0;
      border: none;
    }
    &:nth-child(even){
      align-items: flex-end;
    }
    &-wrap{
      display: flex;
      align-items: center;
    }
    &-num{
      margin-right: 34px;
    }
  }
}

.video{
  position: relative;
  padding-bottom: 56%;
  border-radius: 10px;
  overflow: hidden;
  iframe{
    width: 100%;
    height: 100%;
    position: absolute;
  }
}

.scheme{
  padding: 70px 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 186px) 186px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    display: flex;
    align-items: center;
  }
  &__info{
    background: #2C2C2C;
    border-radius: 10px;
    width: 100%;
    padding: 30px 40px;
  }
  &__item{
    padding: 15px;
    &:not(:first-child){
      margin-top: 9px;
      border-top: 1px solid rgba(#fff,.1);
    }
  }
}

.map{
  margin: 70px 0;
  position: relative;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    position: absolute;
    width: 100%;
    padding: 128px 0;
  }
  &__title{
    max-width: 390px;
    font-size: 52px;
    span{
      color: #fff;
      padding-left: 85px;
      display: inline-block;
    }
  }
}

.collection{
  padding: 70px 0;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    max-width: 290px;
  }
  &__desc{
    max-width: 370px;
  }
  &__slider{
    margin-top: 70px;
  }
  &__nav{
    margin-top: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-button{
      &-prev,
      &-next{
        flex-shrink: 0;
        position: static;
        margin: 0 30px;
        width: 70px;
        height: 70px;
        padding: 0;
        border-radius: 50%;
        opacity: 1 !important;
        &.swiper-button-disabled{
          background-color: rgba(#fff,.1);
        }
      }
    }
    &-pagination{
      position: static;
      width: unset;
      &-title{
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}

.info{
  padding: 100px 0;
  width: 100%;
  max-width: calc(100% - 70px);
  margin: 70px auto;
  background: rgba(255, 255, 255, 0.06);
  border-radius: 16px;
  .collection{
    margin-top: 70px;
  }
  .video{
    padding-bottom: 62.2%;
  }
  .way{
    padding-bottom: 0;
  }
}

.where,
.consultation{
  &__inner{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 100px;
    position: relative;
    border-radius: 10px;
    overflow: hidden;
  }
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    pointer-events: none;
    width: 485px;
  }
}

.where{
  &__inner{
    padding-top: 84px;
    margin-top: 70px;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(90deg, rgba(20, 20, 20, 0.8) 9.32%, rgba(20, 20, 20, 0.31) 67.09%);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 510px;
  }
  &__title{
    max-width: 430px;
    span{
      color: #fff;
    }
  }
  &__desc{
    margin-top: 60px;
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 54px;
  }
  &__btn{
    padding: 0;
    width: 240px;
    flex-shrink: 0;
  }
  &__hint{
    max-width: 180px;
    margin-left: 35px;
  }
}

.top-location{
  margin-top: 140px;
  &__inner{
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 70px 100px 90px;
    display: grid;
    grid-template-columns: 31% 61%;
    gap: 8%;
    border-radius: 10px;
  }
  &__img{
    position: relative;
    img{
      position: absolute;
      width: 100%;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__items{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
  &__icon{
    height: 45px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  &__btn{
    width: 240px;
    padding: 0;
    margin-top: 60px;
  }
}

.questions {
  padding: 70px 0;
  &__title {
    max-width: 315px;
    span {
      display: inline-block;
      padding-left: 85px;
      color: #FFFFFF;
    }
  }
  &__items {
    margin-top: 70px;
  }
  &__item {
    background: #222222;
    border-radius: 10px;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding: 12px 12px 12px 37px;
      &--active {
        .questions__item-btn {
          img{
            transform: rotate(-90deg);
          }
        }
      }
    }
    &-btn {
      width: 45px;
      height: 45px;
      border-radius: 6px;
      margin-left: 10px;
      padding: 0;
      flex-shrink: 0;
      img{
        transition: all .3s;
      }
    }
    &-hidden {
      padding: 9px 100px 30px 70px;
      display: none;
    }
    &-text {
      opacity: 0.5;
    }
  }
}

.consultation {
  position: relative;
  padding: 70px 0 140px;
  &__bg {
    position: absolute;
    top: -25%;
    left: -15%;
    background: #C39F7B;
    opacity: 0.5;
    filter: blur(112px);
    width: 374px;
    height: 374px;
  }
  &__inner {
    background-image: url(../img/consultation-bg.png);

  }

  &__top {
    display: flex;
    justify-content: space-between;
  }
  &__title {
    max-width: 440px;
    span {
      display: inline-block;
      color: #FFFFFF;
      &:last-child {
        padding-left: 85px;
      }
    }
  }
  &__desc {
    max-width: 370px;
  }
  &__grid {
    margin-top: 70px !important;
  }
}

.stage{
  padding: 70px 0;
  &__title{
    span{
      color: #fff;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 22.5px));
    gap: 30px;
    margin-top: 70px;
  }
}

//.difficulties,
.top-location,
.scheme,
.stage,
.questions,
.consultation,
.footer{
  display: none;
}


@media screen and (max-width: 1230px){
  .info{
    max-width: 100%;
  }
}


@media screen and (max-width: 1100px){
  .who{
    &__inner{
      grid-template-columns: calc(38.5% - 7.5px) calc(61.5% - 7.5px);
      gap: 15px;
    }
    &__content{
      padding: 5px 15px;
    }
  }
}



@media screen and (max-width: 992px) {
 .main{
   margin-top: -55px;
   &__inner{
     padding: 94px 0 0;
     display: block;
   }
   &__title{
     max-width: 280px;
   }
   &__info{
     margin-top: 30px;
     &-item{
       &:not(:last-child){
         margin-right: 8px;
       }
     }
     &-num{
       font-size: 22px;
       margin-right: 10px;
     }
   }
   &__bottom{
     margin-top: 30px;
     display: block;
     max-width: 400px;
   }
   &__btn{
     width: 100%;
   }
   &__desc{
     margin: 10px 0 0;
     text-align: center;
     max-width: unset;
   }
   &__img{
     position: relative;
     width: 191px;
     right: unset;
     margin: -31px auto 0;
     &:before{
       width: 183.96px;
       height: 183.94px;
       bottom: -50px;
     }
     &-man{
       transform: translateY(50px);
     }
     &-rounds{
       width: 320px;
     }
   }
   &__owner{
     bottom: 49px;
     z-index: 3;
     right: 50%;
     transform: translateX(50%);
     text-align: center;
     padding: 10px 18px;
   }
   &__name,
   &__co{
     font-size: 12px;
   }
 }

  .who{
    padding: 35px 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__desc,
    &__desc2{
      margin-top: 30px;
    }
    &__logo{
      width: 106px;
    }
  }

  .way{
    padding: 35px 0;
    &__inner{
      padding-top: 78px;
      position: relative;
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__title{
      position: absolute;
      top: 0;
      left: 0;
    }
    &__desc{
      margin: 0;
    }
    &__bottom{
      padding-top: 30px;
    }
    &__items{
      margin-top: 40px;
    }
    &__item{
      padding: 0;
      border: none;
      min-height: 70px;
      &-num{
        margin-right: 10px;
      }
    }
  }

  .collection{
    padding: 35px 0;
    overflow: hidden;
    &__top{
      display: block;
    }
    &__title{
      span{
        display: block;
      }
    }
    &__desc{
      margin-top: 16px;
      max-width: 250px;
    }
    &__slider{
      margin-top: 30px;
    }
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        width: 280px;
        max-width: calc(100% - 5px);
      }
      &-button{
        &-prev,
        &-next{
          margin: 0 12px;
          width: 45px;
          height: 45px;
          svg{
            width: 18px;
            height: auto;
          }
        }
      }
    }
    &__nav{
      margin-top: 30px;
    }
  }

  .info{
    margin: 35px 0;
    padding: 40px 0;

    .collection{
      margin-top: 25px;
    }
  }

  .where{
    &__inner{
      margin-top: 30px;
      padding: 30px;
    }
    &__img{
      display: none;
    }
    &__desc{
      margin-top: 30px;
    }
    &__bottom{
      margin-top: 30px;
      display: block;
    }
    &__btn{
      width: 100%;
    }
    &__hint{
      margin: 10px 0 0;
      text-align: center;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 768px){
  .way{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 576px) {
  .who{
    &__info{
      display: block;
    }
    &__img{
      &:before{
        width: 100%;
        height: 50%;
        top: unset;
        left: 0;
        bottom: 0;
      }
    }
    &__content{
      border-radius: 0 0 10px 10px;
      margin-top: -2px;
      padding: 30px;
    }
    &__logo{
      margin: 0 auto;
    }
  }
}
